// VARIABLES AND MIXINS

$color-white: #ffffff;
$color-light-grey: #F0F3FD;
$color-light-grey-2: #E0E3EB;
$color-dark-grey: #888B97;
$color-black: #000000;

$main-color: #1B1C57;
$added-color: #626687;

$btn-color-light: #D1FAE5;
$btn-color-dark: #10B981;
$btn-font-green: #047857;

$text-shadow: -1px 0 $main-color, 0 1px $main-color, 1px 0 $main-color, 0 -1px $main-color;

$linear-gradient: linear-gradient(16.74deg, #0C1537 -6.09%, rgba(12, 21, 55, 0) 32.63%);

@mixin display-between {
    display: flex;
    justify-content: space-between; }

// COMMON STYLES

*, ::after, ::before {
    margin: 0;
    padding: 0;
    box-sizing: border-box; }

html {
    font-size: 62.5%;
    scroll-behavior: smooth; }

body {
    color: $main-color;
    font-size: 1.6rem;
    font-family: "Lexend", sans-serif;
    background-color: $color-white;
    background-image: url(../img/background-1.png);
    background-repeat: no-repeat;
    background-position: -170px -220px;
    background-size: 800px; }

main {
    overflow: hidden; }

img {
    height: auto;
    display: block; }

h1, h2, h3, h4 {
    margin: 0; }

a {
    color: $color-light-grey;
    font-weight: 600;
    font-size: 14px;
    line-height: 18px;
    text-decoration: none;
    transition: .3s; }

a,
button {
    cursor: pointer; }

p {
    font-size: 1.6rem;
    line-height: 2.8rem;
    text-transform: none; }

ul, ol {
    list-style: none; }

.container {
    width: 129rem;
    margin: 0 auto; }

.radius {
    border-radius: 32px; }

.btn-transparent {
    display: inline-block;
    padding: 7.5px 16.5px;
    background-color: rgba(255, 255, 255, 0.1);
    border: 1px solid rgba(255, 255, 255, 0.3); }

.btn-light_green {
    font-weight: 600;
    font-size: 14px;
    line-height: 22px;
    border: none;
    color: $btn-font-green;
    background-color: #D1FAE5;
    padding: 12px 24.5px; }

.btn-dark_green {
    display: flex;
    align-items: center;
    border: none;
    padding: 13px 16.5px;
    font-size: 14px;
    font-weight: 600;
    line-height: 22px;
    color: $color-white;
    background-color: $btn-color-dark;

    &::after {
        content: "";
        background: url(../icon/arrow.svg) center/cover no-repeat;
        width: 18px;
        height: 18px;
        transform: rotate(-90deg);
        margin-left: 10px; } }

.list {
    display: flex;

    &::after {
        content: "";
        background: url(../icon/arrow.svg) center/cover no-repeat;
        width: 18px;
        height: 18px;
        margin-left: 10px; } }

// HEADER

.main-header {
    position: relative;
    padding-top: 4rem;
    margin-bottom: 9.6rem;

    &__img {
        position: absolute;
        z-index: -1;
        width: 720px;
        height: 720px;
        top: 0px;
        left: 799px;
        background: $linear-gradient, url(../img/hero-img.jpg);
        border-radius: 0 0 0 80px; } }

.main-nav {
    @include display-between;
    align-items: center;
    background-color: transparent;

    &__logo {
        margin-right: auto; } }

.navigation {
    @include display-between;
    align-items: center;
    width: 27%;
    margin-right: 4%; }

// SECTION HERO

.hero {
    position: relative;
    margin-bottom: 12rem;

    &__title {
        width: 30%;
        font-size: 40px;
        font-weight: 600;
        line-height: 50px;
        text-transform: capitalize;
        text-shadow: $text-shadow;
        margin-bottom: 2.4rem;

        span {
            color: $color-white; } }

    &__info {
        width: 38%;
        color: $added-color;
        opacity: 0.75;
        margin-bottom: 3.2rem; }

    &__search {
        position: relative;
        margin-bottom: 3.2rem;

        &::before {
            content: "";
            position: absolute;
            top: 18px;
            left: 28px;
            width: 18px;
            height: 22px;
            background: url(../icon/pin.svg) center/cover no-repeat; }

        input {
            width: 38.2%;
            padding: 18px 64px;
            color: $color-dark-grey;
            font-weight: 500;
            font-size: 14px;
            line-height: 22px;
            border: 1px solid $color-light-grey-2;

            &:focus {
                outline: none; } }

        label {
            position: absolute;
            clip: rect(0 0 0 0); } }

    &__button {
        position: absolute;
        top: 6px;
        left: 376px; }

    &__partnership {
        @include display-between;
        width: 33%; }

    &__partnership-info {
        color: $color-dark-grey;
        font-size: 16px;
        line-height: 32px;
        text-transform: capitalize; }

    &__right {
        @include display-between;
        position: absolute;
        width: 540px;
        height: 114px;
        right: 119px;
        bottom: -50px;

        &_big {
            display: flex;
            padding: 29px 26px;
            background-color: $color-white; }

        &_small {
            display: flex;
            padding: 29px 24px;
            background-color: $color-white;

            img {
                margin-right: 16px; } }

        p {
            color: $main-color;
            font-weight: 600;
            font-size: 16px;
            line-height: 24px;
            text-transform: capitalize; }

        span {
            color: $added-color;
            font-size: 12px;
            line-height: 20px;
            text-transform: capitalize; } } }

.people {
    display: flex;
    width: 112px;
    margin-right: 16px; }

.people li:nth-child(2) {
    transform: translateX(-27px); }

.people li:nth-child(3) {
    transform: translateX(-53px); }
